/* Container */
/* The following rules adapt to the different screens. The container occupy 100% of the screen, with a left and right padding, and automatic left and right margins, so the design is centered.
The media queries allow a maximum width for the container in relation with devices with a minimum width indicated with the breakpoint.
*/

.container-mobile {
  width: 90%;
  margin: 0 auto;
}

/* Tablet Queries */


@media only screen and (max-width: 1180px) {
  figure {
    width: 50%;
    display: inline;
    float: right;
  }

  .portrait {
    width: 100%;
    height: auto;
  }
}

/* Phone Queries */

@media only screen and (max-width: 767px) {
  .more li a {
    font-size: 2rem;
  }

  section {
    margin-bottom: 5rem;
  }

  iframe {
    width: 100%;
  }

  figure {
    width: 100%;
    margin-left: 0;
    display: block;
  }

  .featured {
    margin: 0;
    overflow-x: hidden;
    bottom: 0;
    left: 5%;
    padding: 0;
  }

  #featured-span {
    font-size: 3.5rem;
  }

  footer {
    font-size: 1.3rem;
    text-align: center;
    padding: 2rem 0;
  }

  .legal-links {
    float: none;
    text-align: center;
  }
  .legal-links li {
    margin-left: 0;
    margin-right: 1.5rem;
  }

  .horn-list a {
    font-size: 1.8rem;
  }

  .horn-list li {
    margin-left: 2rem;
    font-size: 1.8rem;
    line-height: 2;
  }

  .dedicatee li {
    font-size: 2rem;
  }

  .dedicatee li a {
    font-size: 2rem;
  }

  .items {
    grid-template-columns: repeat(2, 1fr);
  }

  .instructions {
    font-size: 1.6rem;
  }

  .copyrights a {
    font-size: 2rem;
  }

  h1 {
    font-size: 3.5rem;
    line-height: 1.5;
  }

  .title {
    font-size: 2.5rem;
    margin-bottom: 1rem;
  }

  body {
    font-size: 2rem;
    line-height: 1.7;
  }

  .featured-text {
    font-size: 1.8rem;
    line-height: 1.5;
  }

  h2 {
    font-size: 2.5rem;
  }

  .featured h2 {
    font-size: 2rem;
    margin-bottom: 1rem;
  }

  .portrait {
    width: 100%;
    height: auto;
  }
}



/* When the screen is less than 600 pixels wide, hide all links, except for the first one ("Home"). Show the link that contains should open and close the topnav (.icon) */
@media screen and (max-width: 600px) {
  .topnav a:not(:first-child) {display: none;}
  .topnav a.icon {
    float: right;
    display: block;
  }
}

/* The "responsive" class is added to the topnav with JavaScript when the user clicks on the icon. This class makes the topnav look good on small screens (display the links vertically instead of horizontally) */
@media screen and (max-width: 600px) {
  .topnav.responsive {position: relative;}
  .topnav.responsive a.icon {
    position: absolute;
    right: 5vw;
    top: 0;
  }
  .topnav.responsive a {
    float: none;
    display: block;
    text-align: center;
  }
}